import React from "react";
import styled from "styled-components";

const ClassInformation = ({ classInfo }) => {
  return (
    <InfoPanel
      style={{
        background: "blue",
        minHeight: "10rem",
      }}
    >
      {classInfo && (
        <>
          <Title>{classInfo.courseName}</Title>
          <SubTitle>
            {getSemester(classInfo.year)} Year {getSemester(classInfo.semester)}{" "}
            Semester
          </SubTitle>
        </>
      )}
      <SubTitle style={{ marginTop: "auto", fontSize: "0.89rem" }}>
        {" "}
        Session: {classInfo.session}
      </SubTitle>
      <SubTitle style={{ marginTop: "auto", fontSize: "0.85rem" }}>
        {classInfo.study_center}
      </SubTitle>
      <SubTitle
        style={{
          position: "absolute",
          bottom: "10px",
          fontWeight: 400,
          fontSize: "0.8rem",
        }}
      >
        {classInfo.teacherName}
      </SubTitle>
    </InfoPanel>
  );
};

export default ClassInformation;

const getSemester = (num) => {
  if (num === 1) {
    return "1st";
  } else if (num === 2) {
    return "2nd";
  } else if (num === 3) {
    return "3rd";
  } else if (num === 4) {
    return "4th";
  } else {
    return "";
  }
};

export const InfoPanel = styled.div`
  position: relative;
  padding: 01rem;
  width: 100%;
  border-radius: 5px;
  color: #fff;
  height: 200px;
  margin-bottom: 0.5rem;
`;

export const Title = styled.h1`
  font-size: 1.2rem;
`;

export const SubTitle = styled.h5`
  font-size: 1rem;
`;

export const OptionPanel = styled.div`
  position: absolute;
  right: 5px;
  top: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  width: 35px;
  height: 35px;
  font-size: 1.4rem;
  cursor: pointer;
  &:hover {
    background: rgba(240, 240, 240, 0.4);
  }
`;

export const OptionItems = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 35px;
  top: 15px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 5px 4px rgba(3, 3, 3, 0.2);
`;

export const Li = styled.li`
  color: #333;
  padding: 0.3rem 0.5rem;
  list-style-type: none;
  font-weight: 500;
  cursor: pointer;

  &:nth-last-child(1) {
  }
`;
